<template>
  <div>
    <img src="./images/a.jpg" alt="img" @click='showPreview'>
    <img src="./images/long.png" alt="img" @click='showPreview'>
    <img src="./images/long-small.png" alt="img" @click='showPreview'>
    <img src="./images/width-small.png" alt="img" @click='showPreview'>
    <!-- <pre-view :show="show" :img='img' @hide='hide'></pre-view> -->
    <view-img :show="show" :img='img' @hide='hide'></view-img>
  </div>
</template>

<script>
// import PreView from './PreView'
// import {ViewImg} from 'view-img'
export default {
  name: 'hammer',
  components: {
    // PreView,
    // ViewImg
  },
  data () {
    return {
      show: false,
      img: ''
    }
  },
  methods: {
    showPreview (e) {
      this.img = e.target.currentSrc
      this.show = true
    },
    hide () {
      this.show = false
      this.img = ''
    }
  }
}
</script>

<style>
body{
  margin: 0;
  padding: 0;
}
img{
  max-width: 100%;
}
</style>



